<template>
    <div>
        <top-nav title="砍价活动列表"></top-nav>
        <div class="wyu-tips wyu-tips-warning"  v-loading="loading" v-if="datas.bargain">
            <el-row>
                <el-col :span="4">商品编号：{{datas.bargain.goods_sn}}</el-col>
                <el-col :span="12">商品名称：{{datas.bargain.goods_name}}</el-col>
            </el-row>
            <el-row>
                <el-col :span="4">砍倒底价需要多少人：{{datas.bargain.need_num}}</el-col>
                <el-col :span="6">PLUS会员多砍一刀：{{datas.bargain.is_vip_more_bargain ? '是' : '否'}}</el-col>
                <el-col :span="6">砍价时长(小时)：{{datas.bargain.end_time}}</el-col>
            </el-row>
        </div>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form size="mini" v-model="searchForm" class="search-form" :inline="true">
                    <el-form-item label="用户ID">
                        <el-input v-model="searchForm.user_id" placeholder="用户ID"></el-input>
                    </el-form-item>

                    <el-form-item label="收货人">
                        <el-input v-model="searchForm.name" placeholder="收货人"></el-input>
                    </el-form-item>

                    <el-form-item label="联系方式">
                        <el-input v-model="searchForm.mobile" placeholder="联系方式"></el-input>
                    </el-form-item>

                    <el-form-item label="当前已达到(包括超过)砍价次数">
                        <el-input v-model="searchForm.now_num" placeholder="砍价次数"></el-input>
                    </el-form-item>

                    <el-form-item label="开砍时间">
                        <el-date-picker v-model="searchForm.add_time" type="datetime" placeholder="开砍时间"></el-date-picker>
                    </el-form-item>

                    <el-form-item label="失效时间">
                        <el-date-picker v-model="searchForm.end_time" type="datetime" placeholder="失效时间"></el-date-picker>
                    </el-form-item>

                    <el-form-item label="是否已失效">
                        <el-select v-model="searchForm.is_invalid" placeholder="请选择">
                            <el-option label="请选择" :value="-999"></el-option>
                            <el-option label="未失效" :value="0"></el-option>
                            <el-option label="已失效" :value="1"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="是否已砍完">
                        <el-select v-model="searchForm.is_success" placeholder="请选择">
                            <el-option label="请选择" :value="-999"></el-option>
                            <el-option label="未砍完" :value="0"></el-option>
                            <el-option label="已砍完" :value="1"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="是否已支付">
                        <el-select v-model="searchForm.is_pay" placeholder="请选择">
                            <el-option label="请选择" :value="-999"></el-option>
                            <el-option label="未支付" :value="0"></el-option>
                            <el-option label="已支付" :value="1"></el-option>
                        </el-select>
                    </el-form-item>

                    <div style="padding-left:20px;">
                        <el-button size="mini" type="primary" @click="getList(1)">搜索</el-button>
                        <el-button size="mini" type="text" @click="searchForm.user_id='',searchForm.name='',searchForm.mobile='',
                        searchForm.now_num='',searchForm.end_time='',searchForm.add_time='',
                        searchForm.is_invalid=-999,searchForm.is_success=-999,searchForm.is_pay=-999,getList(1)">取消搜索</el-button>

                    </div>
                </el-form>
                <el-table :data="datas.list" style="width: 100%" size="small">
                    <!--<el-table-column type="selection" width="55"></el-table-column>-->

                    <el-table-column prop="bargain_item_id" label="砍价ID" width="60"></el-table-column>

                    <el-table-column label="用户信息">
                        <template slot-scope="scope">
                            <p>用户ID：<span>{{scope.row.user_id}}</span></p>
                            <p>收货人：<span>{{scope.row.name}}</span></p>
                            <p>联系方式：<span>{{scope.row.mobile}}</span></p>
                        </template>
                    </el-table-column>

                    <el-table-column prop="need_num" label="已砍人数/需要人数"  width="180">
                        <template slot-scope="scope">
                            {{scope.row.now_num}} / {{scope.row.need_num}}
                        </template>
                    </el-table-column>

                    <el-table-column prop="need_num" label="初始价格/当前价格/最终价格"  width="240">
                        <template slot-scope="scope">
                            ￥{{scope.row.init_price}} / ￥{{scope.row.now_price}} / ￥{{scope.row.bargain_price}}
                        </template>
                    </el-table-column>

                    <el-table-column prop="add_time" label="开砍时间" width="240">
                        <template slot-scope="scope">
                            <p>过期时间:{{scope.row.end_time}}</p>
                            <p>开砍时间:{{scope.row.add_time}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column prop="is_invalid" label="状态" >
                        <template slot-scope="scope">
                            {{scope.row.is_invalid == 1 ? '已失效' : '正常'}}
                        </template>
                    </el-table-column>

                    <el-table-column prop="is_success" label="是否砍成功" >
                        <template slot-scope="scope">
                            {{scope.row.is_success == 1 ? '已砍完' : '未砍完'}}
                        </template>
                    </el-table-column>

                    <el-table-column prop="is_pay" label="是否支付" >
                        <template slot-scope="scope">
                            {{scope.row.is_pay == 1 ? '已支付' : '未支付'}}
                        </template>
                    </el-table-column>

                    <el-table-column label="操作" >
                        <template slot-scope="scope">
                            <el-col :span="8">
                                <el-button size="mini" type="text" @click="showLog(scope)">查看帮砍记录</el-button>
                            </el-col>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="page">
                    <div class="page-mian">
                        <el-pagination @current-change="getList" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>
        <el-dialog title="帮砍记录" :visible.sync="dialog" width="500px" center :modal-append-to-body="false">
            <el-table :data="helpData" style="width: 100%" size="small">
                <el-table-column prop="user_id" label="用户ID" width="60"></el-table-column>
                <el-table-column prop="nickname" label="昵称" width="200"></el-table-column>
                <el-table-column prop="face" label="头像" width="100">
                    <template slot-scope="scope">
                        <img :src="scope.row.face" width="50px">
                    </template>
                </el-table-column>
                <el-table-column prop="price" label="砍掉价格" >
                    <template slot-scope="scope">
                        ￥ {{scope.row.price}}
                    </template>
                </el-table-column>
            </el-table>
        </el-dialog>
    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";
    import DateBetween from "@/components/DateBetween";

    export default {
        name: 'goodsAttrIndex',
        components: {TopNav,DateBetween},
        data() {
            return {
                bargain_id:0,
                //整体菊花
                loading: false,
                typeLoading: false,
                dialog: false,
                //列表数据
                datas: {},
                //搜索数据
                searchForm: {
                    user_id: '',
                    name: '',
                    mobile: '',
                    now_num: '',
                    add_time: '',
                    end_time: '',
                    is_invalid: -999,
                    is_pay: -999,
                    is_success: -999,
                },
                helpData: [],
            }
        },

        created() {
            this.bargain_id = this.$route.params.id;
            this.getList();
        },

        methods: {
            getList(page = 1) {
                this.loading = true;
                api.post("/shop/admin.marketing.bargain/join", {
                    appToken: this.$store.getters.appToken,
                    bargain_id: this.$route.params.id,
                    user_id: this.searchForm.user_id,
                    name: this.searchForm.name,
                    mobile: this.searchForm.mobile,
                    now_num: this.searchForm.now_num,
                    add_time: this.searchForm.add_time,
                    end_time: this.searchForm.end_time,
                    is_invalid: this.searchForm.is_invalid,
                    is_pay: this.searchForm.is_pay,
                    is_success: this.searchForm.is_success,
                    page: page,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },


            showLog(scope) {
                this.dialog = true;
                console.log(scope.row.bargain_log);
                this.helpData = scope.row.bargain_log;

            },

        }
    }
</script>

